.modal-overlay {
  display: none;
  @media (min-width: 900px) {
    box-sizing: border-box;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
  }
}

.modal {
  position: relative;
  width: 800px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.5) 0 6px 24px 0, rgba(0, 0, 0, 0.8) 0 0 0 1px;

  &__close-button {
    position: absolute;
    font-size: 20px;
    border: none;
    background-color: transparent;
    right: 10px;
    top: 10px;

    &:hover {
      color: gray;
    }
  }
}

.modal-container {
  display: flex;
  min-height: 224px;
  flex-direction: row;
  align-items: center;
}

.modal-hero {
  margin-bottom: 60px;
  max-height: 400px;
}

.modal__image {
  position: relative;
  width: 95%;
  max-width: 600px;
  padding: 60px;
  padding-top: 92px;
  padding-left: 20px;
  max-width: 600px;
  padding-bottom: 0px;
  z-index: 20;
}

.modal__bubble {
  background-color: hsl(231, 69%, 60%);
  position: relative;
  height: 188px;
  width: 78%;
  max-width: 291px;
  /* top: 105px; */
  margin-left: 0px;
  left: 0;
  bottom: 135px;
  z-index: 7;
  border-bottom-right-radius: 100px;
  border-top-right-radius: 200px;
}

.modal__container {
  padding-right: 30px;
}

.modal__button {
  text-decoration: none;
  background-color: hsl(231, 69%, 60%);
  color: white;
  padding: 15px 20px;
  border-radius: 5px;
  font-size: 15px;
  border: 1px solid transparent;
  display: inline-block;
}
